import './index.scss';
import create_hidden_input from "../../module/create_hidden_input/create_hidden_input";
import { change_counter } from '../../module/shop_scripts';

let slickFinder0 = $(".slider_card-wrap").length;
if (slickFinder0 >= 1) {
    $(".main_card_slider").slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        fade: true,
        arrows: false,
        asNavFor: ".mini_slider",
        lazyLoad: "ondemand",
    });
    let width = window.innerWidth;
    let mini_slider_settings = {};

    if (width > 992) {
        mini_slider_settings = {
            infinite: true,
            slidesToShow: 3,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            focusOnSelect: true,
            asNavFor: ".main_card_slider",
            lazyLoad: "ondemand",
        }
    } else {
        mini_slider_settings = {
            infinite: true,
            slidesToShow: 3,
            slidesToScroll: 1,
            vertical: false,
            verticalSwiping: true,
            focusOnSelect: true,
            asNavFor: ".main_card_slider",
            lazyLoad: "ondemand",
        }
    }


    $(".mini_slider").slick(mini_slider_settings);


    $('.slider_navigation_right').on('click', function () {
        $(".main_card_slider").slick('slickNext');
    });


    $('.slider_navigation_left').on('click', function () {
        $(".main_card_slider").slick('slickPrev');
    });

}

$(".item_tab_link").on("click", function () {
    ($(this)[0].dataset.tab);
    var className = ($(this)[0].dataset.tab);
    console.log(className);
    ($(".item_tab_link").removeClass("item_tab_link_active"));
    ($(this).addClass("item_tab_link_active"));
    ($(".item_tab_content").removeClass("item_tab_content_active"));
    ($("#" + $(this)[0].dataset.tab).addClass("item_tab_content_active"));

});



$('.item_description_btn').on('click', function () {
    let item_id = Number($(this).attr('data-id-name'));
    let body = {
        "item_id": item_id,
    }


    fetch('/api/cart_items/', {
        method: 'POST',
        body: JSON.stringify(body),
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json"
        }
    })
        .then(data => {
            return data.json();
        })
        .then(data => {
            change_counter(data.cart_items_quantity, null);
        });
})

